<template>
  <div class="post-container">
    <img class="bg" src="https://staging-sapporobeer-cdn.augcloud.com/images/share-card.jpg" @load="loadImage" />
    <img :class="{ 'qrcode-img': true, 'show-qrcode': showQrcode }" :src="qrcodeImg" />
    <img class="html-to-img" v-show="html2img" :src="html2img" />
  </div>
</template>

<script>
import QRCode from 'qrcode'
import html2canvas from 'html2canvas'
import { guid } from '../../utils/string'

export default {
  name: 'post',
  components: {
  },
  data: () => ({
    qrcodeImg: '',
    html2img: null,
    showQrcode: false
  }),

  created () {
    window.gtag('event', 'page_view', { page_title: '海报页', content: '海报页' })
    const shareUrl = `${window.location.href.replace('post', '')}?shareId=${guid()}`
    console.log(shareUrl)
    QRCode.toDataURL(shareUrl, {
      margin: 0,
      color: {
        dark: '#f6c54e',
        light: '#000'
      }
    }).then(url => {
      this.qrcodeImg = url
    }).catch(err => {
      console.error(err)
    })
  },

  methods: {
    loadImage: function () {
      this.showQrcode = true
      this.$nextTick(() => {
        html2canvas(document.getElementsByClassName('post-container')[0]).then((canvas) => {
          this.html2img = canvas.toDataURL('image/jpg')
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
@import './post.less';
</style>



