<template>
  <div class="landing-container">
    <img class="top-img" src="https://staging-sapporobeer-cdn.augcloud.com/images/landing-top.png">
    <img class="bottom-img" src="https://staging-sapporobeer-cdn.augcloud.com/images/landing-bottom.png">
    <div class="btn" @click="confirm"></div>
  </div>
</template>

<script>
export default {
  created () {
    const { shareId } = this.$route.query
    if (shareId) {
      window.gtag('event', 'page_view', { page_title: 'above 18 页(分享进入)', content: 'above 18 页(分享进入)', share_id: shareId })
    } else {
      window.gtag('event', 'page_view', { page_title: 'above 18 页', content: 'above 18 页' })
    }
  },
  methods: {
    confirm: function () {
      window.gtag('event', 'click', { content: '点击确认已满18岁' })
      this.$router.push({ name: 'ar' })
    }
  }
}
</script>

<style scoped lang="less">
@import './landing.less';
</style>



