import EventEmitter from '../../utils/eventEmitter'
import { MIN_SIM } from './data'

const tracking = {
  /**
   * Tracks a canvas, image or video element based on the specified `tracker`
   * instance. This method extract the pixel information of the input element
   * to pass to the `tracker` instance. When tracking a video, the
   * `tracker.track(pixels, width, height)` will be in a
   * `requestAnimationFrame` loop in order to track all video frames.
   *
   * Example:
   * let tracker = new tracking.ColorTracker()
   *
   * tracking.track('#video', tracker)
   * or
   * tracking.track('#video', tracker, { camera: true })
   *
   * tracker.on('track', function (event) {
   *   // console.log(event.data[0].x, event.data[0].y)
   * })
   *
   * @param {HTMLElement} element The element to track, canvas, image or
   *     video.
   * @param {tracking.Tracker} tracker The tracker instance used to track the
   *     element.
   */
  track: function (element, tracker, canvas) {
    switch (element.nodeName.toLowerCase()) {
      case 'video':
        return this.trackVideo_(element, tracker, canvas)
      default:
        throw new Error('Element not supported, try in a canvas, img, or video.')
    }
  },

  /**
   * Tracks a canvas element based on the specified `tracker` instance. This
   * method extract the pixel information of the input element to pass to the
   * `tracker` instance.
   * @param {HTMLCanvasElement} element Canvas element to track.
   * @param {tracking.Tracker} tracker The tracker instance used to track the
   *     element.
   * @param {object} opt_options Optional configuration to the tracker.
   * @private
   */
  trackCanvasInternal_: function (width, height, context, tracker, minsim) {
    const imageData = context.getImageData(0, 0, width, height).data
    tracker.track(imageData, width, height, minsim)
  },

  /**
   * Tracks a video element based on the specified `tracker` instance. This
   * method extract the pixel information of the input element to pass to the
   * `tracker` instance. The `tracker.track(pixels, width, height)` will be in
   * a `requestAnimationFrame` loop in order to track all video frames.
   * @param {HTMLVideoElement} element Canvas element to track.
   * @param {tracking.Tracker} tracker The tracker instance used to track the
   *     element.
   * @param {object} opt_options Optional configuration to the tracker.
   * @private
   */
  trackVideo_: function (element, tracker) {
    const minsim = MIN_SIM
    const canvas = document.createElement('canvas')
    const context = canvas.getContext('2d')
    let width
    let height

    const resizeCanvas_ = function () {
      width = element.offsetWidth
      height = element.offsetHeight
      canvas.width = width
      canvas.height = height
    }
    resizeCanvas_()
    element.addEventListener('resize', resizeCanvas_)

    let requestId
    const requestAnimationFrame_ = () => {
      requestId = window.requestAnimationFrame(requestAnimationFrame_)
      context.drawImage(element, 0, 0, width, height)
      this.trackCanvasInternal_(width, height, context, tracker, minsim)
    }

    const task = new TrackerTask(tracker)
    task.on('stop', () => { window.cancelAnimationFrame(requestId) })
    task.on('run', () => { requestAnimationFrame_() })

    task.on('checkPack', (worker) => {
      const imageData = context.getImageData(0, 0, width, height).data
      worker.postMessage({ cmd: 'start', msg: { pixels: imageData, width, height } })
    })

    return task.run()
  }
}

class Utils {
  static targetColorCheck (r, g, b) {
    // let computedH = 0
    // r = r / 255
    // g = g / 255
    // b = b / 255
    // const minRGB = Math.min(r, Math.min(g, b))
    // const maxRGB = Math.max(r, Math.max(g, b))

    // if (minRGB !== maxRGB) {
    //   const d = (r === minRGB) ? g - b : ((b === minRGB) ? r - g : b - r)
    //   const h = (r === minRGB) ? 3 : ((b === minRGB) ? 1 : 5)
    //   computedH = 60 * (h - d / (maxRGB - minRGB))
    // }

    // return Math.abs(computedH - 30) < 20
    return b <= 128 && r - g > 24 && g - b > 24 && r > 64
  }
}

/**
 * Tracker utility.
 * @constructor
 * @extends {EventEmitter}
 */
class Tracker extends EventEmitter {
}

/**
 * TrackerTask utility.
 * @constructor
 * @extends {EventEmitter}
 */
class TrackerTask extends EventEmitter {
  constructor (tracker) {
    super()
    /**
     * Holds the tracker instance managed by this task.
     * @type {tracker}
     * @private
     */
    if (!tracker) {
      throw new Error('Tracker instance not specified.')
    }
    /**
     * Holds if the tracker task is in running.
     * @type {boolean}
     * @private
     */
    this.running_ = false
    this.setTracker(tracker)
  }

  /**
   * Gets the tracker instance managed by this task.
   * @return {Tracker}
   */
  getTracker () {
    return this.tracker_
  }

  /**
   * Returns true if the tracker task is in running, false otherwise.
   * @return {boolean}
   * @private
   */
  inRunning () {
    return this.running_
  }

  /**
   * Sets if the tracker task is in running.
   * @param {boolean} running
   * @private
   */
  setRunning (running) {
    this.running_ = running
  }

  /**
   * Sets the tracker instance managed by this task.
   * @return {Tracker}
   */
  setTracker (tracker) {
    this.tracker_ = tracker
  }

  /**
   * Emits a `run` event on the tracker task for the implementers to run any
   * child action, e.g. `requestAnimationFrame`.
   * @return {object} Returns itself, so calls can be chained.
   */
  run () {
    const self = this

    if (this.inRunning()) {
      return
    }

    this.setRunning(true)
    this.reemitTrackEvent_ = function (event) {
      self.emit('track', event)
    }
    this.tracker_.on('track', this.reemitTrackEvent_)
    this.emit('run')
    return this
  }

  /**
   * Emits a `stop` event on the tracker task for the implementers to stop any
   * child action being done, e.g. `requestAnimationFrame`.
   * @return {object} Returns itself, so calls can be chained.
   */
  stop () {
    if (!this.inRunning()) {
      return
    }

    this.setRunning(false)
    this.emit('stop')
    this.tracker_.removeListener('track', this.reemitTrackEvent_)
    return this
  }
}

/**
 * ColorTracker utility to track colored blobs in a frame using color
 * difference evaluation.
 * @constructor
 * @param {string|Array.<string>} optColors Optional colors to track.
 * @extends {Tracker}
 */
class ColorTracker extends Tracker {
  constructor (image) {
    super()
    // Caching neighbour i/j offset values.
    this.neighboursI = new Int32Array([-1, -1, 0, 1, 1, 1, 0, -1])
    this.neighboursJ = new Int32Array([0, 1, 1, 1, 0, -1, -1, -1])
    /**
     * Caches coordinates values of the neighbours surrounding a pixel.
     * @type {Object.<number, Int32Array>}
     * @private
     * @static
     */
    this.neighbours_ = {}
    /**
      * Holds the minimum dimension to classify a rectangle.
      * @default 20
      * @type {number}
      */
    this.minDimension = 20
    /**
      * Holds the maximum dimension to classify a rectangle.
      * @default Infinity
      * @type {number}
      */
    this.maxDimension = Infinity
    /**
      * Holds the minimum group size to be classified as a rectangle.
      * @default 30
      * @type {number}
      */
    this.minGroupSize = 30

    this.image = image
    if (!(image instanceof Image)) {
      console.warn('target image not exists, just track color.')
    }
    this.canvas = document.createElement('canvas')
  }

  /**
   * Calculates the central coordinate from the cloud points. The cloud points
   * are all points that matches the desired color.
   * @param {Array.<number>} cloud Major row order array containing all the
   *     points from the desired color, e.g. [x1, y1, c2, y2, ...].
   * @param {number} total Total numbers of pixels of the desired color.
   * @return {object} Object containing the x, y and estimated z coordinate of
   *     the blog extracted from the cloud points.
   * @private
   */
  calculateDimensions_ (cloud, total) {
    let maxx = -1
    let maxy = -1
    let minx = Infinity
    let miny = Infinity

    for (let c = 0; c < total; c += 2) {
      const x = cloud[c]
      const y = cloud[c + 1]

      if (x < minx) {
        minx = x
      }
      if (x > maxx) {
        maxx = x
      }
      if (y < miny) {
        miny = y
      }
      if (y > maxy) {
        maxy = y
      }
    }

    return {
      width: maxx - minx,
      height: maxy - miny,
      x: minx,
      y: miny,
      maxx,
      maxy
    }
  }

  /**
   * Gets the minimum dimension to classify a rectangle.
   * @return {number}
   */
  getMinDimension () {
    return this.minDimension
  }

  /**
   * Gets the maximum dimension to classify a rectangle.
   * @return {number}
   */
  getMaxDimension () {
    return this.maxDimension
  }

  /**
   * Gets the minimum group size to be classified as a rectangle.
   * @return {number}
   */
  getMinGroupSize () {
    return this.minGroupSize
  }

  /**
   * Gets the eight offset values of the neighbours surrounding a pixel.
   * @param {number} width The image width.
   * @return {array} Array with the eight offset values of the neighbours
   *     surrounding a pixel.
   * @private
   */
  getNeighboursForWidth_ (width) {
    if (this.neighbours_[width]) {
      return this.neighbours_[width]
    }

    const neighbours = new Int32Array(8)

    neighbours[0] = -width * 4
    neighbours[1] = -width * 4 + 4
    neighbours[2] = 4
    neighbours[3] = width * 4 + 4
    neighbours[4] = width * 4
    neighbours[5] = width * 4 - 4
    neighbours[6] = -4
    neighbours[7] = -width * 4 - 4

    this.neighbours_[width] = neighbours

    return neighbours
  }

  /**
   * Tracks the `Video` frames. This method is called for each video frame in
   * order to emit `track` event.
   * @param {Uint8ClampedArray} pixels The pixels data to track.
   * @param {number} width The pixels canvas width.
   * @param {number} height The pixels canvas height.
   */
  track (pixels, width, height, minsim) {
    const result = this.trackColor_(pixels, width, height, minsim)

    this.emit('track', result)
  }

  /**
   * Find the given color in the given matrix of pixels using Flood fill
   * algorithm to determines the area connected to a given node in a
   * multi-dimensional array.
   * @param {Uint8ClampedArray} pixels The pixels data to track.
   * @param {number} width The pixels canvas width.
   * @param {number} height The pixels canvas height.
   * @param {string} color The color to be found
   * @private
   */
  trackColor_ (pixels, width, height, minsim) {
    let currGroup = new Int32Array(pixels.length >> 2) // 2382524 / 4
    let currGroupSize
    let currI
    let currJ
    let currW
    let marked = new Int8Array(pixels.length) // 2382524 (-2^7 to 2^7-1)
    let minGroupSize = this.getMinGroupSize() // default 30
    // 893, Int32Array(8) [-3572, -3568, 4, 3576, 3572, 3568, -4, -3576]
    let neighboursW = this.getNeighboursForWidth_(width)
    let queue = new Int32Array(pixels.length) // 2382524 (-2^31 to 2^31-1)
    let queuePosition
    let result = { width: 47 } // 返回宽度最大区域
    let w = -4

    // 列，行
    for (let i = 0; i < height; i++) {
      for (let j = 0; j < width; j++) {
        w += 4

        if (marked[w]) {
          continue
        }

        currGroupSize = 0
        queuePosition = -1
        queue[++queuePosition] = w
        queue[++queuePosition] = i
        queue[++queuePosition] = j
        marked[w] = 1

        while (queuePosition >= 0) {
          currJ = queue[queuePosition--]
          currI = queue[queuePosition--]
          currW = queue[queuePosition--]

          if (Utils.targetColorCheck(pixels[currW], pixels[currW + 1], pixels[currW + 2])) {
            currGroup[currGroupSize++] = currJ
            currGroup[currGroupSize++] = currI

            for (let k = 0, len = neighboursW.length; k < len; k++) {
              let otherW = currW + neighboursW[k]
              let otherI = currI + this.neighboursI[k]
              let otherJ = currJ + this.neighboursJ[k]
              if (!marked[otherW] && otherI >= 0 && otherI < height && otherJ >= 0 && otherJ < width) {
                queue[++queuePosition] = otherW
                queue[++queuePosition] = otherI
                queue[++queuePosition] = otherJ
                marked[otherW] = 1
              }
            }
          }
        }

        if (currGroupSize >= minGroupSize) {
          let data = this.calculateDimensions_(currGroup, currGroupSize)
          // MARK : set rectangles min and max size
          const { width, height } = data
          if (width > result.width && height >= 48) {
            const fingerprint = this.getFingerprint(width + 1, height + 1)
            const boolMatrix = new Uint8Array((width + 1) * (height + 1))
            for (let c = 0; c < currGroupSize; c += 2) {
              let x = currGroup[c] - data.x
              let y = currGroup[c + 1] - data.y
              boolMatrix[y * (width + 1) + x] = 1
            }
            let hammingWeight = 0
            for (let i = 0, len = boolMatrix.length; i < len; i++) {
              boolMatrix[i] === fingerprint[i] && hammingWeight++
            }
            if (hammingWeight / boolMatrix.length >= minsim) {
              result = data
            }
          }
        }
      }
    }

    return result.width === 47 ? {} : result
  }

  /**
   * Find target image fingerprint
   * @param {number} width The pixels canvas width.
   * @param {number} height The pixels canvas height.
   * @return {[width * height]} 0, 1 array
   * @private
   */
  getFingerprint (width, height) {
    this.image.width = width
    this.image.height = height
    this.canvas.width = width
    this.canvas.height = height
    const ctx = this.canvas.getContext('2d')
    ctx.drawImage(this.image, 0, 0, width, height)
    const imageData = ctx.getImageData(0, 0, width, height).data
    const fingerprint = new Uint8Array(width * height)
    for (let i = 0, len = imageData.length; i < len; i += 4) {
      const r = imageData[i]
      const g = imageData[i + 1]
      const b = imageData[i + 2]
      if (r > 120 && g > 60 && r >= b * 2.4 && r > g && g >= 2 * b) {
        fingerprint[i / 4] = 1
      }
    }
    return fingerprint
  }
}

tracking['ColorTracker'] = ColorTracker
tracking['TrackerTask'] = TrackerTask

export default tracking



