import tracking from './tracking'
import {
  getInitSceneMatrix,
  getScaleMatrix,
  getTranslateMatrix
} from './matrix4ar'
import { PACK } from './data'
import Worker from './web.worker.js'

const worker = new Worker()

class Controller {
  constructor ({ video, inputWidth, inputHeight, inputLeft, postMatrix, imageTarget, onUpdate, onPackChanged }) {
    this.inputLeft = inputLeft
    this.onUpdate = onUpdate
    this.onPackChanged = onPackChanged
    const near = 10
    const far = 10000
    const fovy = 45.0 * Math.PI / 180 // 45 in radian. field of view vertical
    const f = (inputHeight / 2) / Math.tan(fovy / 2)
    //     [fx  s cx]
    // K = [ 0 fx cy]
    //     [ 0  0  1]
    const projectionTransform = [
      [f, 0, inputWidth / 2],
      [0, f, inputHeight / 2],
      [0, 0, 1]
    ]

    this.projectionMatrix = this._glProjectionMatrix({
      projectionTransform: projectionTransform,
      width: inputWidth,
      height: inputHeight,
      near: near,
      far: far
    })

    this.trackerTask = this._loadColorTracker(postMatrix, imageTarget, video)
    worker.postMessage({
      cmd: 'init',
      msg: {
        windowWidth: window.innerWidth,
        windowHeight: window.innerHeight
      }
    })
  }

  getProjectionMatrix () {
    return this.projectionMatrix
  }

  processVideo () {
    this.trackerTask.run()
  }

  stopProcessVideo () {
    this.trackerTask.stop()
  }

  dispose () {
    this.trackerTask.removeAllListeners()
    worker.postMessage({ cmd: 'stop' })
  }

  checkPack () {
    this.trackerTask.emit('checkPack', worker)
  }

  _loadColorTracker (postMatrix, imageTarget, video) {
    let pack = 'can'
    worker.onmessage = ({ data }) => {
      pack = data
      console.log('worker.onmessage', data)
      this.onPackChanged(data)
    }

    const packConfig = PACK
    const tracker = new tracking.ColorTracker(imageTarget)
    const trackerTask = tracking.track(video, tracker)
    trackerTask.stop()
    // 96px <=> 1(scale)
    // const initScaleWidth = 96
    const lastData = { x: 0, y: 0 }
    let packOffsetHeight
    const initSceneMatrix = getInitSceneMatrix(postMatrix, 1)

    tracker.on('track', ({ width, height, x, y }) => {
      if (width) {
        packOffsetHeight = packConfig[pack].offsetHeight

        if ((lastData.x === x + this.inputLeft && y !== lastData.y) ||
          (lastData.x !== x + this.inputLeft && y === lastData.y)) {
          return
        }
        lastData.x = x + this.inputLeft
        lastData.y = y

        const scale = Math.max(width, height) / 96
        let worldMatrix = getScaleMatrix(scale, initSceneMatrix)

        const translateX = x + this.inputLeft - (window.innerWidth - width) / 2
        const translateY = -y + (window.innerHeight - height) / 2 - packOffsetHeight * scale
        worldMatrix = getTranslateMatrix({ x: translateX * 4.743, y: translateY * 4.743 }, worldMatrix)

        this.onUpdate(worldMatrix)
      } else {
        this.onUpdate(null)
      }
    })

    return trackerTask
  }

  // build openGL projection matrix
  // ref: https://strawlab.org/2011/11/05/augmented-reality-with-OpenGL/
  _glProjectionMatrix ({ projectionTransform, width, height, near, far }) {
    const proj = [
      [2 * projectionTransform[0][0] / width, 0, -(2 * projectionTransform[0][2] / width - 1), 0],
      [0, 2 * projectionTransform[1][1] / height, -(2 * projectionTransform[1][2] / height - 1), 0],
      [0, 0, -(far + near) / (far - near), -2 * far * near / (far - near)],
      [0, 0, -1, 0]
    ]
    const projMatrix = []
    for (let i = 0; i < 4; i++) {
      for (let j = 0; j < 4; j++) {
        projMatrix.push(proj[j][i])
      }
    }
    return projMatrix
  }
}

export default Controller



