import { getImgURL, isIOS } from '../../utils/string'
import starImg from '../../../static/star.png' // 图比较小，代码打包时会编译为 basse64

export const TEXTURE_DATAS = ((isIOS) => {
  const datas = {}
  if (isIOS) {
    for (let i = 1; i <= 8; i++) {
      const bgItem = { image: `https://staging-sapporobeer-cdn.augcloud.com/images/spritescomb_${i}-585.png`, next: `mb_${i + 1}`, iInfo: [7, 49] }
      if (i === 1) {
        bgItem['autoStart'] = true
      } else if (i === 8) {
        bgItem['isLast'] = true
        bgItem['iInfo'] = [7, 41]
        bgItem.next = 'mb_1'
      }
      datas[`mb_${i}`] = bgItem
    }
  } else {
    for (let i = 1; i <= 24; i++) {
      const bgItem = { basis: `https://staging-sapporobeer-cdn.augcloud.com/images/etc1s_${i}.basis`, next: `mb_${i + 1}`, iInfo: [4, 16] }
      if (i === 1) {
        bgItem['autoStart'] = true
      } else if (i === 24) {
        bgItem['isLast'] = true
        bgItem.next = 'mb_1'
      }
      datas[`mb_${i}`] = bgItem
    }
  }
  return datas
})(isIOS())

export const FPS = 0.012

export const MIN_SIM = 0.75

export const IMAGE_TARGET_SRC = getImgURL(starImg)

export const BGM = 'https://staging-sapporobeer-cdn.augcloud.com/media/Sapporo_AR-Parade_Music.wav'

export const PACK = {
  can: { topRadius: 1, bottomRadius: 0.8, offsetHeight: 120 },
  bottle: { topRadius: 1, bottomRadius: 1, offsetHeight: 110 }
}



