/**
  * 进入页面的初始化：
  * 1. 纹理
  * 2. 3D 场景
  * 3. 协助 UI (loading, scaning)
  * 4. 目标形状图片(此处为五角星) (checkArReady)
  * 5. video
  * 6. 注入场景
  * 点击 “开始 AR”
  * 1. 图片播放准备 (checkArReady)
  * 2. 打开相机
  * 3. 初始化 AR（yuxi,抛出事件：initedAR）
  * 4. 更新相机，开始识别（view,捕获事件：initedAR）
*/
import * as THREE from 'three'
import { BasisTextureLoader } from 'three/examples/jsm/loaders/BasisTextureLoader.js'
import YuXi from './yuxi'
import { TEXTURE_DATAS, BGM, PACK, FPS } from './data'
import { isIOS } from '../../utils/string'

class View {
  constructor (eventEmitter, rootId, options) {
    this.eventEmitter = eventEmitter
    this.textures = {}
    this.textureAnimators = {}
    this.geometry = null
    this.camera = null
    this.scene = null
    this.renderer = null
    this.group = null
    this.oneLoop = false
    this.firstKeys = new Set()
    this.curKeys = new Set()
    this.playFlag = 0 // 0, 从未播放过；1，未播放；2，播放中；3，暂停中
    this.hasReady = false // 点击 SCAN 或者 纹理图片加载完成时，设置为 true，再次 check 时，做第一次渲染
    this.sound = null
    this.yuxi = new YuXi(eventEmitter, rootId, options)
    this._initAnimateAndListeners()

    const soundListener = this._loadSound()
    let loadMethod
    let group
    if (isIOS()) {
      loadMethod = this._loadTextureImage
    } else {
      group = this._initRenderer(rootId, soundListener)
      loadMethod = this._loadBasisTexture
    }
    loadMethod.call(this, () => {
      console.log('_loadTexture')
      this.yuxi.registerAnchor(group)
      console.log('registerAnchor finished, checkArReady')
      this.yuxi.checkArReady()
      this.firstRender()

      delete this._initScene
      delete this._loadTextureImage
      delete this.textures
      delete this._flipY
      delete this._initRenderer
      delete this._loadBasisTexture
    })
    !group && (group = this._initScene(rootId, soundListener))
  }

  _loadSound () {
    // create an AudioListener and add it to the camera
    const listener = new THREE.AudioListener()

    // create a global audio source
    const sound = new THREE.Audio(listener)

    // load a sound and set it as the Audio object's buffer
    const audioLoader = new THREE.AudioLoader()
    const _self = this
    audioLoader.load(BGM, (buffer) => {
      sound.setBuffer(buffer)
      sound.setLoop(false)
      _self.yuxi.checkArReady()
      console.log('_loadSound, checkArReady')
    })
    _self.sound = sound

    return listener
  }

  _loadBasisTexture (loadedCallback) {
    let loadedImageCount = 0
    const fps = FPS
    const _self = this

    const { originMaterial, geometry, group, renderer, textureAnimators, firstKeys, curKeys, eventEmitter, yuxi } = this
    delete this.originMaterial
    const loader = new BasisTextureLoader()
    loader.setTranscoderPath('https://staging-sapporobeer-cdn.augcloud.com/libs/basis/')
    loader.detectSupport(renderer)

    for (const key in TEXTURE_DATAS) {
      if (Object.hasOwnProperty.call(TEXTURE_DATAS, key)) {
        const data = TEXTURE_DATAS[key]
        data.autoStart && firstKeys.add(key)

        loader.load(data.basis, (texture) => {
          loadedImageCount++

          texture.wrapS = THREE.RepeatWrapping
          texture.wrapT = THREE.RepeatWrapping
          texture.repeat.set(1 / data.iInfo[0], 1 / data.iInfo[0])
          texture.offset.set(0, 0)
          texture.needsUpdate = true

          const material = originMaterial.clone()
          material.map = texture
          material.needsUpdate = true

          group.add(new THREE.Mesh(geometry, material))
          const [x, y] = TEXTURE_DATAS[key].iInfo
          const textureAnimator = new TextureAnimator(key, { x, y }, fps, material, (key) => {
            curKeys.delete(key)
            const textureAnimator = textureAnimators[key]

            if ((!TEXTURE_DATAS[key].isLast || !_self.oneLoop) && textureAnimator.next) {
              textureAnimator.stop()
              textureAnimators[textureAnimator.next].start()
              curKeys.add(textureAnimator.next)
            } else {
              textureAnimator.stop()
              eventEmitter.emit('loopFinished')
              _self.playFlag = 0
              yuxi.pause(true)
            }
          })

          if (TEXTURE_DATAS[key].autoStart) {
            textureAnimator['autoStart'] = true
          }
          textureAnimator['next'] = TEXTURE_DATAS[key].next
          _self.textureAnimators[key] = textureAnimator

          console.log('loadingTextureImage, ', loadedImageCount)
          loadedImageCount === Object.keys(TEXTURE_DATAS).length && loadedCallback()
          // render()
        }, undefined, function (error) {
          console.error(error)
        })
      }
    }
  }

  /**
   * 根据精灵图初始化纹理 (图片加载，位置)
   * @param {loadedCallback} function 纹理图片全部加载完成时回调
   */
  _loadTextureImage (loadedCallback) {
    let loadedImageCount = 0
    for (const key in TEXTURE_DATAS) {
      if (Object.hasOwnProperty.call(TEXTURE_DATAS, key)) {
        const data = TEXTURE_DATAS[key]
        data.autoStart && this.firstKeys.add(key)

        const texture = new THREE.TextureLoader().load(data.image, () => {
          loadedImageCount++
          console.log('loadingTextureImage, ', loadedImageCount)
          loadedImageCount === Object.keys(TEXTURE_DATAS).length && loadedCallback()
        }, () => {}, (e) => { console.log(e) })
        texture.wrapS = THREE.RepeatWrapping
        texture.wrapT = THREE.RepeatWrapping

        // 纹理图片帧行数
        const colCount = Math.ceil(data.iInfo[1] / data.iInfo[0])
        // 纹理单元 (1 / 帧列数，1 / 帧行数)
        texture.repeat.set(1 / data.iInfo[0], 1 / colCount)
        // 第一帧位置（上至下，右至左）
        texture.offset.set(0, (colCount - 1) / colCount)
        // texture.needUpdate = true
        this.textures[key] = texture
      }
    }
  }

  _initRenderer (rootId, listener) {
    const container = document.getElementById(rootId)
    const camera = new THREE.PerspectiveCamera(75, container.clientWidth / container.clientHeight, 0.1, 1000)
    const renderer = new THREE.WebGLRenderer({ alpha: true, antialias: true })
    renderer.setSize(container.clientWidth, container.clientHeight)
    renderer.setPixelRatio(window.devicePixelRatio)
    container.appendChild(renderer.domElement)
    camera.add(listener)
    const scene = new THREE.Scene()
    const { topRadius, bottomRadius } = PACK['can']
    const geometry = new THREE.CylinderBufferGeometry(topRadius, bottomRadius, Math.PI * topRadius, 16, 4, true, -Math.PI / 2, Math.PI)
    geometry.name = 'can'
    this.geometry = this._flipY(geometry)
    this.originMaterial = new THREE.MeshBasicMaterial({ transparent: true, opacity: 0 })
    const group = new THREE.Group()

    this.camera = camera
    this.renderer = renderer
    scene.add(group)
    this.scene = scene
    this.group = group

    return group
  }

  /**
   * 初始化相机，材质，纹理承载体，渲染器，场景
   * @param {rootId} String 渲染根节点 Id
   */
  _initScene (rootId, listener) {
    const scene = new THREE.Scene()
    const container = document.getElementById(rootId)
    const camera = new THREE.PerspectiveCamera(75, container.clientWidth / container.clientHeight, 0.1, 1000)
    const renderer = new THREE.WebGLRenderer({ alpha: true, antialias: true })
    renderer.setSize(container.clientWidth, container.clientHeight)
    renderer.setPixelRatio(window.devicePixelRatio)
    container.appendChild(renderer.domElement)
    camera.add(listener)

    const { topRadius, bottomRadius } = PACK['can']
    const geometry = new THREE.CylinderBufferGeometry(topRadius, bottomRadius, Math.PI * topRadius, 16, 4, true, 93.8 / Math.PI, Math.PI)
    geometry.name = 'can'
    this.geometry = geometry
    const originMaterial = new THREE.MeshBasicMaterial({ transparent: true, opacity: 0 })
    const group = new THREE.Group()

    const curKeys = this.curKeys
    const _self = this
    const fps = FPS
    for (const key in _self.textures) {
      if (Object.hasOwnProperty.call(_self.textures, key)) {
        const material = originMaterial.clone()
        material.map = _self.textures[key]
        const mesh = new THREE.Mesh(geometry, material)
        group.add(mesh)
        const iInfo = TEXTURE_DATAS[key].iInfo
        const textureAnimator = new TextureAnimator(key, { x: iInfo[0], y: iInfo[1] }, fps, material, (key) => {
          curKeys.delete(key)
          const textureAnimator = _self.textureAnimators[key]

          if ((!TEXTURE_DATAS[key].isLast || !_self.oneLoop) && textureAnimator.next) {
            textureAnimator.stop()
            _self.textureAnimators[textureAnimator.next].start()
            curKeys.add(textureAnimator.next)
          } else {
            textureAnimator.stop()
            _self.eventEmitter.emit('loopFinished')
            _self.playFlag = 0
            _self.yuxi.pause(true)
          }
        })

        if (TEXTURE_DATAS[key].autoStart) {
          textureAnimator['autoStart'] = true
        }
        textureAnimator['next'] = TEXTURE_DATAS[key].next
        _self.textureAnimators[key] = textureAnimator
      }
    }

    scene.add(group)
    this.camera = camera
    this.scene = scene
    this.renderer = renderer
    this.group = group
    // group.visible = false
    // group.matrixAutoUpdate = false

    return group
  }

  // 根据 video 和 初始化的 3DObject 数据矩阵更新相机
  _updateCamera (proj, clientHeight, vh) {
    this.camera.fov = 2 * Math.atan(1 / proj[5] / vh * clientHeight) * 180 / Math.PI // vertical fov
    this.camera.near = proj[14] / (proj[10] - 1.0)
    this.camera.far = proj[14] / (proj[10] + 1.0)
    this.camera.updateProjectionMatrix()
  }

  _initAnimateAndListeners () {
    let _self = this
    let reqId
    const clock = new THREE.Clock()
    const animate = () => {
      reqId = window.requestAnimationFrame(animate)
      const delta = clock.getDelta() * 1000
      _self.curKeys.forEach(item => {
        _self.textureAnimators[item].update(delta)
      })
      _self.renderer.render(_self.scene, _self.camera)
    }

    // AR 初始化后，更新相机，开始识别
    this.eventEmitter.on('initedAR', ({ pro, clientHeight, vh }) => {
      console.log('update threejs camera')
      this._updateCamera(pro, clientHeight, vh)
      console.log('ALL READY FINISHED')
      this.yuxi.start()
      this._visibilitychange = () => { document.hidden ? this.playFlag > 0 && this.eventEmitter.emit('pauseView') : this.playFlag > 1 && this.eventEmitter.emit('unpauseView') }
      document.addEventListener('visibilitychange', this._visibilitychange, false)

      delete this._updateCamera
    }, { once: true })

    this.eventEmitter.on('pauseView', (playFlag) => {
      console.log('pauseView')
      _self.yuxi.pause()
      _self.sound.pause()
      window.cancelAnimationFrame(reqId)
      _self.playFlag = Number.isInteger(playFlag) ? playFlag : 3
    })

    this.eventEmitter.on('unpauseView', () => {
      console.log('unpauseView')
      _self.yuxi.play()
      _self.sound.play()
      window.requestAnimationFrame(animate)
      _self.playFlag = 2
    })

    this.eventEmitter.on('targetLost', (playFlag) => {
      console.log('targetLost')
      _self.curKeys.forEach(item => {
        _self.textureAnimators[item].stop()
      })
      _self.sound.pause()
      _self.renderer.render(_self.scene, _self.camera)
      window.cancelAnimationFrame(reqId)
      _self.playFlag = Number.isInteger(playFlag) ? playFlag : 1
    })

    this.eventEmitter.on('targetFound', () => {
      console.log('targetFound')
      !_self.playFlag && _self.yuxi.ui.hideScanning()
      _self.renderer.render(_self.scene, _self.camera)
      _self.playFlag = 2
      _self.curKeys.clear()
      _self.firstKeys.forEach(key => _self.curKeys.add(key))
      _self.curKeys.forEach((key) => { _self.textureAnimators[key].start() })
      window.requestAnimationFrame(animate)
      _self.sound.play(0)
    })

    this.eventEmitter.on('packChanged', (pack) => {
      const targetPack = pack || 'can'
      if (this.geometry.name !== targetPack) {
        const { topRadius, bottomRadius } = PACK[targetPack]
        const targetGeometry = new THREE.CylinderBufferGeometry(topRadius, bottomRadius, Math.PI * topRadius, 16, 4, true, 93.8 / Math.PI, Math.PI)
        this.geometry.attributes = targetGeometry.attributes
        this.geometry.name = targetPack
        targetGeometry.dispose()
      }
    })
  }

  _flipY (geometry) {
    const uv = geometry.attributes.uv
    for (let i = 0; i < uv.count; i++) {
      uv.setY(i, 1 - uv.getY(i))
    }
    return geometry
  }

  /**
   * @param {oneLoop} boolean 是否只播放一次
   */
  play (oneLoop = true) {
    this.oneLoop = !!oneLoop

    if (!this.camera || this.group) {
      this.yuxi.checkArReady()
      this.yuxi.video.play()
      this.firstRender()
    } else {
      this.yuxi.start()
    }
    console.log('play, ', 'checkArReady')
  }

  // 使第一次渲染在 AR 开启之前
  firstRender () {
    if (this.hasReady) {
      const startTime = (new Date()).getTime()
      this.renderer.render(this.scene, this.camera)
      console.log((new Date()).getTime() - startTime)
      this.group.visible = false
      this.group.matrixAutoUpdate = false
      delete this.group
    } else {
      this.hasReady = true
    }
  }

  stop () {
    this.yuxi.pause()
    this.yuxi.anchorEntity.object3D.visible = false
    this.playFlag = 0
    this.eventEmitter.emit('targetLost', 0)
  }

  // 释放资源
  dispose () {
    console.log('dispose')
    this.eventEmitter.emit('pauseView', 0)
    this.curKeys.clear()
    Object.keys(this.textureAnimators).forEach(key => {
      this.textureAnimators[key].material.map.dispose()
      this.textureAnimators[key].material.dispose()
      delete this.textureAnimators[key].material
    })
    this.geometry.dispose()
    this.yuxi.stop()
    this.eventEmitter.removeAllListeners()
    delete this.eventEmitter
    delete this.textureAnimators
    delete this.geometry
    delete this.camera
    delete this.scene
    delete this.renderer
    delete this.oneLoop
    delete this.firstKeys
    delete this.curKeys
    delete this.sound
    document.removeEventListener('visibilitychange', this._visibilitychange, false)
    delete this._visibilitychange
  }
}

/**
 * 当前图片展示模型纹理播放类
 * @param {key} string 唯一标识当前类
 * @param {rowFrameCount} int 精灵图行数
 * @param {totalFrameCount} int 数据帧总数
 * @param {material} Material 模型材质
 * @param {nextCallback} function 当前纹理播放完毕时的回调函数
 */
class TextureAnimator {
  constructor (key, { x: rowFrameCount, y: totalFrameCount }, fps, material, nextCallback) {
    this.rowFrameCount = rowFrameCount
    this.totalFrameCount = totalFrameCount
    this.material = material
    this.nextCallback = nextCallback
    this.colFrameCount = rowFrameCount
    // this.colFrameCount = Math.ceil(totalFrameCount / rowFrameCount)
    this.currentDisplayTime = 0
    this.xFC = 0
    this.yFC = 0
    this.lastCount = 0
    this.key = key
    this.fps = fps
  }

  /**
   * 更新展示的图片帧
   * @param {milliSec} number 毫秒
   */
  update (milliSec) {
    if (!this.canUpdate) {
      return
    }

    this.currentDisplayTime += milliSec
    // 每 40 ms 执行一次，误差 + (milliSec-1)
    // * 12 / 1000,  * 0.025
    const curCount = Math.floor(this.currentDisplayTime * this.fps)
    if (curCount > this.lastCount) {
      this.lastCount = curCount

      // 左至右，下至上
      this.material.map.offset.x = this.xFC++ / this.rowFrameCount
      this.material.map.offset.y = this.yFC / this.colFrameCount

      if (this.yFC * this.rowFrameCount + this.xFC === this.totalFrameCount) {
        // 已移动到最后一帧
        this.nextCallback(this.key)
      } else if (this.xFC === this.rowFrameCount) {
        // 移动一行
        this.xFC = 0
        this.yFC++
      }
    }
  }

  reset () {
    this.xFC = 0
    this.yFC = 0
    this.material.map.offset.x = 0
    this.material.map.offset.y = 0
  }

  stop () {
    this.material.opacity = 0
    this.canUpdate = false
    this.reset()
  }

  pause () {
    this.canUpdate = false
  }

  start () {
    this.material.opacity = 1
    this.canUpdate = true
  }
}

export default View



