// eslint-disable-next-line
import '!style-loader!css-loader!less-loader!./ui.less'
import loadingHTML from './loading.html'
import scanningHTML from './scanning.html'

class UI {
  constructor () {
    this.loadingModal = this._loadHTML(loadingHTML)
    this.scanningModal = this._loadHTML(scanningHTML)
  }

  // init ({ uiScanning }) {
  //   // eslint-disable-next-line
  //   uiScanning !== 'yes' && (this.scanningModal.remove(), delete this.scanningModal)
  // }

  showLoading () {
    this.loadingModal?.classList.remove('hidden')
  }

  hideLoading () {
    this.loadingModal?.classList.add('hidden')
  }

  showScanning () {
    this.scanningModal?.classList.remove('hidden')
  }

  hideScanning () {
    this.scanningModal?.classList.add('hidden')
  }

  remove () {
    this.loadingModal?.remove()
    this.scanningModal?.remove()
    delete this.loadingModal
    delete this.scanningModal
  }

  _loadHTML (html) {
    const e = document.createElement('template')
    e.innerHTML = html.trim()
    const rootNode = e.content.firstChild
    document.getElementsByTagName('body')[0].appendChild(rootNode)
    return rootNode
  }
}

export default UI



