<template>
  <div id="app" :class="{ 'black-bg': showBlackBg }">
    <router-view/>
  </div>
</template>

<script>
import vhCheck from 'vh-check'
vhCheck()

export default {
  name: 'App',
  data: () => ({
    showBlackBg: true
  }),
  mounted () {
    this.showBlackBg = this.$route.name !== 'ar'
  },
  watch: {
    '$route': function (newVal) {
      this.showBlackBg = newVal.name !== 'ar'
    }
  }
}
</script>

<style lang="less">
@import './styles/mixins.less';

#app {
  position: relative;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: calc(100vh - var(--vh-offset, 0px));
  width: 100vw;
  text-align: center;
  color: #2c3e50;
}

.black-bg::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  z-index: -1;
}

body {
  margin: 0;
}
</style>



