import * as THREE from 'three'
import UI from './ui/ui'
import Controller from './controller'
import { IMAGE_TARGET_SRC } from './data'

let startTime // TODO: remove
class YuXi {
  constructor (eventEmitter, nodeId) {
    this.container = document.getElementById(nodeId)
    this.video = null
    this.readyFlag = 4
    this.eventEmitter = eventEmitter
    this.ui = null
    this.canTrack = false
    this.starting = false

    this._init()
  }

  _init () {
    this._initAssistUI()
    this._initTargetImage()
    this._initVideo()

    delete this._initAssistUI
    delete this._initTargetImage
    delete this._initVideo
  }

  _initAssistUI () {
    this.ui = new UI()
    this.ui.showLoading()
    // this.ui.init({ uiLoading, uiScanning })
  }

  _initTargetImage () {
    const _self = this
    const imageTarget = new Image()
    imageTarget.onload = () => {
      _self.checkArReady()
      console.log('_initTargetImage, checkArReady')
    }
    imageTarget.src = IMAGE_TARGET_SRC
    this.imageTarget = imageTarget
  }

  _initVideo () {
    this.video = document.createElement('video')

    this.video.setAttribute('autoplay', '')
    this.video.setAttribute('muted', '')
    this.video.setAttribute('playsinline', '')
    this.video.style.position = 'absolute'
    this.video.style.top = '0px'
    this.video.style.left = '0px'
    this.video.style.zIndex = '-2'
    this.container.appendChild(this.video)

    if (navigator.mediaDevices === undefined) {
      navigator.mediaDevices = {}
    }

    if (!navigator.mediaDevices.getUserMedia) {
      const getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia ||
        navigator.mozGetUserMedia || navigator.msGetUserMedia
      if (!getUserMedia) {
        this.eventEmitter.emit('mediaError', { error: 'VIDEO_FAIL' })
        return
      }
      navigator.mediaDevices.getUserMedia = getUserMedia
    }
  }

  _initAR () {
    const video = this.video
    const container = this.container

    let vw, vh // display css width, height
    const videoRatio = video.videoWidth / video.videoHeight
    const containerRatio = container.clientWidth / container.clientHeight
    if (videoRatio > containerRatio) {
      vh = container.clientHeight
      vw = vh * videoRatio
    } else {
      vw = container.clientWidth
      vh = vw / videoRatio
    }

    this.video.style.top = `${(-(vh - container.clientHeight) / 2)}px`
    this.video.style.left = `${(-(vw - container.clientWidth) / 2)}px`
    this.video.style.width = `${vw}px`
    this.video.style.height = `${vh}px`

    const targetDimension = [525, 717]
    this.anchorEntity.setupMarker(targetDimension)

    let _self = this
    this.controller = new Controller({
      video,
      inputWidth: video.videoWidth,
      inputHeight: video.videoHeight,
      inputLeft: video.offsetLeft,
      postMatrix: _self.anchorEntity.postMatrix.elements,
      imageTarget: _self.imageTarget,
      onUpdate: (worldMatrix) => { _self.anchorEntity.updateWorldMatrix(worldMatrix) },
      onPackChanged: (pack) => { _self.eventEmitter.emit('packChanged', pack) }
    })

    this.eventEmitter.emit('initedAR', { pro: this.controller.getProjectionMatrix(), clientHeight: container.clientHeight, vh })

    delete this.imageTarget

    // const newCam = new window.AFRAME.THREE.PerspectiveCamera(fov, newRatio, near, far)
    // camera.getObject3D('camera').projectionMatrix = newCam.projectionMatrix
  }

  _setupCatchStream (loadedCallback) {
    navigator.mediaDevices.getUserMedia({
      audio: false,
      video: { facingMode: 'environment' }
    }).then((stream) => {
      this.video.addEventListener('loadedmetadata', () => {
        this.video.setAttribute('width', this.video.videoWidth)
        this.video.setAttribute('height', this.video.videoHeight)
        loadedCallback()
      })
      this.video.srcObject = stream
    }).catch((err) => {
      console.error('getUserMedia error', err)
      this.eventEmitter.emit('mediaError', {error: 'VIDEO_FAIL'})
    })
  }

  _start (scene) {
    console.log('开始调取相机')
    const _self = this
    this._setupCatchStream(() => { _self._initAR(_self.eventEmitter, scene) })
  }

  startTrack () {
    if (this.starting) {
      this.anchorEntity.object3D.visible = false
      this.controller.processVideo()
    }
    this.canTrack = true
  }

  checkArReady () {
    if (!--this.readyFlag) {
      console.log('arReady')
      // this.eventEmitter.emit('arReady')
      this._start()
    }
  }

  registerAnchor (object3D) {
    this.anchorEntity = new ArImageTarget(this.eventEmitter, object3D)
    this.eventEmitter.on('targetFound', () => {
      startTime = (new Date()).getTime()
      setTimeout(() => { this.controller.checkPack() }, 100)
    })
    this.eventEmitter.on('loopFinished', () => {
      console.log((new Date()).getTime() - startTime)
    }, { once: true })
  }

  start () {
    this.ui.hideLoading()
    this.ui.showScanning()
    this.starting = true
    this.anchorEntity.object3D.visible = false
    this.anchorEntity.targetLostTime = 0
    this.canTrack ? this.play() : this.video.play()
  }

  play () {
    this.video.play()
    this.controller.processVideo()
  }

  stop () {
    this.ui.remove()
    if (this.readyFlag) {
      return
    }
    this.pause()
    const tracks = this.video.srcObject.getTracks()
    tracks.forEach(function (track) { track.stop() })
    this.video.remove()
    this.controller.dispose()
    this.anchorEntity.dispose()
    delete this.video
    delete this.controller
    delete this.anchorEntity
    delete this.eventEmitter
    delete this.container
    delete this.ui
  }

  pause (keepVideo = false) {
    !keepVideo && this.video.pause()
    this.controller ? this.controller.stopProcessVideo() : console.warn('controller not inited')
  }
}

class ArImageTarget {
  constructor (eventEmitter, object3D) {
    this.postMatrix = undefined // rescale the anchor to make width of 1 unit = physical width of card
    this.eventEmitter = eventEmitter
    this.object3D = object3D
    this.targetLostTime = 0
  }

  setupMarker ([markerWidth, markerHeight]) {
    const position = new THREE.Vector3()
    const quaternion = new THREE.Quaternion()
    const scale = new THREE.Vector3()
    position.x = markerWidth / 2
    position.y = markerWidth / 2 + (markerWidth - markerHeight) / 2
    scale.x = markerWidth
    scale.y = markerWidth
    scale.z = markerWidth
    this.postMatrix = new THREE.Matrix4()
    this.postMatrix.compose(position, quaternion, scale)
  }

  updateWorldMatrix (worldMatrix) {
    if (!this.object3D.visible && !!worldMatrix) {
      this.eventEmitter.emit('targetFound')
      this.object3D.visible = !!worldMatrix
      this.targetLostTime = 0
    } else if (this.object3D.visible && !worldMatrix) {
      if (!this.targetLostTime) {
        this.targetLostTime = (new Date()).getTime()
      } else if ((new Date()).getTime() - this.targetLostTime >= 3000) {
        this.targetLostTime = 0
        this.object3D.visible = !!worldMatrix
        this.eventEmitter.emit('targetLost')
      }
    }

    if (!worldMatrix) {
      return
    }
    this.targetLostTime && (this.targetLostTime = 0)

    const m = new THREE.Matrix4()
    m.elements = worldMatrix
    m.multiply(this.postMatrix)
    this.object3D.matrix = m
  }

  dispose () {
    delete this.postMatrix
    delete this.eventEmitter
    delete this.object3D
  }
}

export default YuXi



